@import url('https://fonts.googleapis.com/css?family=Inconsolata');

.pageContainer {
  font-family: 'Inconsolata', monospace;
  height: 100vh;
  padding: 2%;
}

.blogList {
  display: flex;
  flex-direction: column;
  background: white;
  /* margin: 0px 5%; */
  border-radius: 0px 0px 5px 5px;
  box-lines: 5px;
}
.blogItem {
  font-size: 1.2em;
  border-top: gray solid 1px;
  padding: 15px 0;
  text-align: center;
}
.twoColumnContainer {
  display: flex;
  flex-direction: row;
}
.leftColumnContainer {
  width: 30%; 
  max-width: 30%; 
  min-width: 30%; 
  margin-right: 20px;
}
.rightColumnContainer {
  width: 70%;
  position: relative;
  background: white;
  margin-left: 20px;
}
.blogContent {
  /* background: lightblue; */
  padding: 5vw 10px 10px 5vw;
  /* margin: 0% 5%; */
  width: auto;
}
